/* ==========================================================================
   Blog List shortcode responsive style - begin
   ========================================================================== */

@include laptop-landscape-large {
    
    .edgtf-blog-list-holder {
        
        &.edgtf-bl-masonry {
            
            $masonry_columns_number: ('five');
            
            @for $i from 0 to length($masonry_columns_number) {
                &.edgtf-bl-#{nth($masonry_columns_number,$i+1)}-columns {
                    $column_width: 100%/4;
    
                    .edgtf-bl-item,
                    .edgtf-bl-grid-sizer {
                        width: $column_width;
                    }
                }
            }
        }
    }
}

@include laptop-landscape-medium {
    
    .edgtf-blog-list-holder {
        
        &.edgtf-bl-standard,
        &.edgtf-bl-boxed {
    
            $columns_number: ('four', 'five');
            
            @for $i from 0 to length($columns_number) {
                &.edgtf-bl-#{nth($columns_number,$i+1)}-columns {
                    $column_width: 100% / 3;
    
                    .edgtf-bl-item {
                        width: $column_width;
                    }
                    
                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                        
                        .edgtf-bl-item {
                            
                            &:nth-child(3n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
        
        &.edgtf-bl-masonry {
            
            $masonry_columns_number: ('four', 'five');
            
            @for $i from 0 to length($masonry_columns_number) {
                &.edgtf-bl-#{nth($masonry_columns_number,$i+1)}-columns {
                    $column_width: 100% / 3;
                    
                    .edgtf-bl-item,
                    .edgtf-bl-grid-sizer {
                        width: $column_width;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .edgtf-blog-list-holder {
        
        &.edgtf-bl-standard,
        &.edgtf-bl-boxed {
            
            $columns_number: ('three', 'four', 'five');
            
            @for $i from 0 to length($columns_number) {
                &.edgtf-bl-#{nth($columns_number,$i+1)}-columns {
                    
                    .edgtf-bl-item {
                        width: 50%;
                    }
                    
                    @media only screen and (min-width: $phone-landscape-plus-pixel) {
                        
                        .edgtf-bl-item {
                            
                            &:nth-child(2n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
        
        &.edgtf-bl-masonry {
            
            $masonry_columns_number: ('three', 'four', 'five');
            
            @for $i from 0 to length($masonry_columns_number) {
                &.edgtf-bl-#{nth($masonry_columns_number,$i+1)}-columns {
                    
                    .edgtf-bl-item,
                    .edgtf-bl-grid-sizer {
                        width: 50%;
                    }
                }
            }
        }
    }
}

@include ipad-portrait{
    .edgtf-blog-list-holder {
        &.edgtf-bl-alternating{
            .edgtf-bli-content{
                .edgtf-bli-content-holder{
                    width: 100%;
                    left: 0;
                }
            }

            .edgtf-post-image{
                width: 100%;
                position: relative;
                padding: 0;

                a{
                    &:after{
                        width: 100%;
                    }
                }
            }
        }
    }
}

@include phone-landscape {
    
    .edgtf-blog-list-holder {
        
        &.edgtf-bl-standard,
        &.edgtf-bl-boxed {
            
            .edgtf-bl-item {
                width: 100% !important; // !important is set to override all other stronger selectors
            }
        }
        
        &.edgtf-bl-masonry {
            
            $masonry_columns_number: ('two', 'three', 'four', 'five');
            
            @for $i from 0 to length($masonry_columns_number) {
                &.edgtf-bl-#{nth($masonry_columns_number,$i+1)}-columns {
                    
                    .edgtf-bl-item,
                    .edgtf-bl-grid-sizer {
                        width: 100%;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   Blog List shortcode responsive style - end
   ========================================================================== */